import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Keyboard } from '@ionic-native/keyboard/ngx';
import { IonicModule } from '@ionic/angular';
import {
  DaonFaceService,
  DaonVoiceService
} from '@nequi/apps/base/mobile/config/features/commons/services/daon';
import {
  HardwarePermissionService
} from '@nequi/apps/base/mobile/config/features/commons/services/hardware-permission';
import { FeaturesAccountLocksModule } from '@nequi/features/account-locks/presentation';
import { MyQrBottomSheetService, QrReaderService } from '@nequi/features/commons/presentation';
import { AccountInteractor, AccountRepository } from '@nequi/features/profile/core';
import { BiometryInteractor, BiometryRepository } from '@nequi/features/security/biometry/core';
import { ChooseBiometryModule } from '@nequi/features/security/biometry/presentation';
import { SettingRepository } from '@nequi/features/settings/core';
import { DaonFacePlugin, DaonVoicePlugin } from '@nequi/native/daon';
import { HardwarePermissionPlugin } from '@nequi/native/hardware-permission';
import { NequiUiModule, SimpleListBottomSheetService } from '@nequi/ui';
import { TranslateModule } from '@ngx-translate/core';

import { AuthRoutingModule } from './auth-routing.module';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    AuthRoutingModule,
    IonicModule,
    NequiUiModule,
    TranslateModule,
    FeaturesAccountLocksModule.forChild(),
    ChooseBiometryModule,
  ],
  providers: [
    DaonFaceService,
    DaonVoiceService,
    DaonFacePlugin,
    DaonVoicePlugin,
    HardwarePermissionService,
    HardwarePermissionPlugin,
    MyQrBottomSheetService,
    SimpleListBottomSheetService,
    QrReaderService,
    Keyboard,
    {
      provide: BiometryInteractor,
      deps: [BiometryRepository],
      useClass: BiometryInteractor,
    },
    {
      provide: AccountInteractor,
      deps: [AccountRepository, SettingRepository],
      useClass: AccountInteractor,
    },
  ],
})
export class AuthModule {}
