import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nConfig } from '@config/i18n.config';
import { I18nModuleResolver, ModuleWithProvidersFactory } from '@nequi/features/commons';

import { SIGN_IN_CONFIG } from '../../features/auth';

const routes: Routes = [
  {
    path: '',
    loadChildren: () =>
      import('@nequi/features/security/auth/presentation').then(
        (m) => new ModuleWithProvidersFactory(m.FeaturesAuthModule.forChild(SIGN_IN_CONFIG))
      ),
  },
  {
    path: 'choose-biometry',
    loadChildren: () =>
      import('@nequi/features/security/biometry/presentation/choose-biometry').then(
        (m) => m.ChooseBiometryModule
      ),
  },
  {
    path: 'biometry-change-password',
    loadChildren: () =>
      import('@nequi/features/security/biometry/presentation/change-password').then(
        (m) => m.ChangePasswordModule
      ),
  },
  {
    path: 'enroll',
    loadChildren: () =>
      import('@nequi/features/enroll/presentation').then((m) => m.FeaturesEnrollModule),
  },
  {
    path: 'registry-lite',
    loadChildren: () =>
      import('@nequi/features/lite-registry/presentation').then((m) => m.RegistryLiteModule),
  },

  {
    path: 'reactivate-account',
    loadChildren: () =>
      import('@nequi/features/security/reactivate-account/presentation').then(
        (m) => m.FeaturesReactivateAccountModule
      ),
  },

  // --------------------------
  // - ADD YOUR ROUTERS ABOVE -
  // --------------------------
  {
    path: '',
    redirectTo: '/auth',
    pathMatch: 'full',
  },
  {
    path: '**',
    redirectTo: '/auth',
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(
      I18nModuleResolver.setup(routes, [
        I18nConfig.modules.SIGN_IN,
        I18nConfig.modules.PAYMENTS,
        I18nConfig.modules.SECURITY,
        I18nConfig.modules.ACCOUNT_LOCKS,
      ])
    ),
  ],
  exports: [RouterModule],
})
export class AuthRoutingModule {}
